import axios from 'axios'
const URL = `${process.env.REACT_APP_BASE_URL}/api/sitefeature`
// const URL = `http://localhost:3001/api/product`

const addPromotion = async (data, token) => {
    const config = {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${token}`
      },
      body: data
    }
  
    try {
      const response = await fetch(`${URL}/add-promotion`, config)
      // Check if the response is unsuccessful
      if (!response.ok) {
        const data = await response.json()
        throw new Error(`${JSON.stringify(data)}`)
      }
      // Parse JSON response
      const data = await response.json() 
      return data
    } catch (error) {
      throw error
    }
  }
  
const getPromotions = async () => {
    const config = {
      method: 'GET'
    }
  
    try {
      const response = await fetch(`${URL}/get-promotions`, config)
      // Check if the response is unsuccessful
      if (!response.ok) {
        const data = await response.json()
        throw new Error(`${JSON.stringify(data)}`)
      }
      // Parse JSON response
      const data = await response.json() 
      return data
    } catch (error) {
      throw error
    }
  }
  
const getPromotionsChunk = async (params) => {
    const response = await axios.post(`${URL}/get-promotions-chunk`, params)
    return response
}

const removePromotion = async(token, id) => {
    const config = {
      method: 'DELETE'
    }
  
    try {
      const response = await fetch(`${URL}/remove-promotion/${id}`, config)
      // Check if the response is unsuccessful
      if (!response.ok) {
        const data = await response.json()
        throw new Error(`${JSON.stringify(data)}`)
      }
      // Parse JSON response
      const data = await response.json() 
      console.log(data)
      return data
    } catch (error) {
      throw error
    }
    
}

export const promotionService = {
    addPromotion,  
    getPromotions, 
    removePromotion,  
    getPromotionsChunk
}