import axios from 'axios'
const URL = `${process.env.REACT_APP_BASE_URL}/api/order`

// const URL = `http://localhost:3001/api/order`

const postOrder = async (orders) => {
    const response = await axios.post(`${URL}/post-order`, orders)
    return response.data
}

const getOrders = async () => {
    const response = await axios.get(`${URL}/get-orders`)
    return response.data
}

const getOrderChunk = async (params) => {
    const response = await axios.post(`${URL}/get-orders-chunk`, params)
    return response
}

const getSingleOrder = async (order_id) => {
    const response = await axios.get(`${URL}/get-single-order/${order_id}`)
    return response.data
}

const updateStatus = async (data) => {
    const response = await axios.put(`${URL}/update-status/${data.id}`, data)
    return response.data
}
const postShippingDetails = async (data, token) => {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    console.log(config)
    const response = await axios.put(`${URL}/post-shipping-detail/${data.id}`, data, config)
    return response.data
}
const postRefundDetails = async (data, token) => {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = await axios.put(`${URL}/post-refund-detail/${data.id}`, data, config)
    return response.data
}


const getSales = async () => {
    const response = await axios.get(`${URL}/get-total`)
    return response.data
}

const orderService = {
    postOrder, 
    getOrders,
    getOrderChunk,
    getSingleOrder, 
    updateStatus, 
    getSales,
    postShippingDetails,
    postRefundDetails

}


export default orderService