import { createSlice, createAsyncThunk } from "@reduxjs/toolkit"
import { footerService } from "./footerService"
const initialState = {
  footers: [],  
  p_isLoading: false,  
  isSuccess: false,  
  isError: false,  
  message: "",
  data: [],
  total: 1,
  params: {}
}

export const addFooter = createAsyncThunk(
  "footer/add",  
  async (productData, thunkApi) => {
  try {
  return await footerService.addFooter(productData)
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)

export const getFooters = createAsyncThunk(
  "footer/get",  
  async (_, thunkApi) => {
  try {
  return await footerService.getFooters()
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)
export const getFootersChunk = createAsyncThunk('footer/get-chunk', async (params, thunkApi) => {
  try {
  const response = await footerService.getFootersChunk(params)
  return {
    params,  
    data: response.data.users,  
    totalPages: response.data.total
    }
  } catch (error) {
  const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
  return thunkApi.rejectWithValue(message)
  }
})
//remove category api
export const removeFooter = createAsyncThunk(
  "footer/remove-banner",  
  async (id, thunkApi) => {
  try {
  const token = thunkApi.getState().auth.userData.accessToken
  return await footerService.removeFooter(token, id)
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)

export const footerSlice = createSlice({
  name: "footer",  
  initialState,  
  reducers: {
  reset: (state) => {
  state.p_isLoading = false
  state.isSuccess = false
  state.isError = false
  state.message = ""
  }
  },  
  extraReducers: (builder) => {
  builder
  .addCase(addFooter.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(addFooter.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(addFooter.fulfilled, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = true
    state.footers.push(action.payload)
  })
  .addCase(getFooters.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(getFooters.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(getFooters.fulfilled, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = true
    console.log(action.payload)
    state.footers = action.payload
  })
  .addCase(removeFooter.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(removeFooter.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(removeFooter.fulfilled, (state, action) => {
    console.log(action)
    state.p_isLoading = false
    state.isSuccess = true
    state.message = action.payload
    //  // Find the index of the deleted record
    const deletedRecordIndex = state.footers.findIndex(
    (product) => product._id === action.payload._id
    )

    // // If the deleted record is found in the allUsers array, remove it
    if (deletedRecordIndex !== -1) {
    state.footers.splice(deletedRecordIndex, 1)
    }
  })
  .addCase(getFootersChunk.fulfilled,  (state,  action) => {
    console.log(action.payload)
    state.data = action.payload.data === undefined ? [] : action.payload.data
    state.params = action.payload.params
    state.total = action.payload.totalPages
  })
  }
})
export const { reset } = footerSlice.actions

export default footerSlice.reducer
