import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import orderService from './orderService'

// set the initialState
const initialState = {
    orders: [], 
    isLoading: false, 
    isSuccess: false, 
    isError: false, 
    message: '', 
    sales:'',
    data: [],
    total: 1,
    params: {}
}

export const postOrder = createAsyncThunk('order/post', async (order, thunkApi) => {
    try {
        
    return await orderService.postOrder(order)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const getOrders = createAsyncThunk('order/get', async (_, thunkApi) => {
    try {
        return await orderService.getOrders()
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
export const getOrderChunk = createAsyncThunk('order/get-chunk-order',  async (params,  thunkApi) => {
    try {
        const response = await orderService.getOrderChunk(params)
        // return response
        return {
            params,
            data: response.data.users,
            totalPages: response.data.total
          }
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
export const getSales = createAsyncThunk('order/sales', async (_, thunkApi) => {
    try {
        return await orderService.getSales()
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
export const getSingleOrder = createAsyncThunk('order/get-single', async (order_id, thunkApi) => {
    try {
        return await orderService.getSingleOrder(order_id)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
export const updateStatus = createAsyncThunk('order/update-status', async (data, thunkApi) => {
    try {
        return await orderService.updateStatus(data)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
export const postShippingDetails = createAsyncThunk('order/post-shipping', async (data, thunkApi) => {
    try {       
        const token = thunkApi.getState().auth.userData.accessToken
        return await orderService.postShippingDetails(data, token)
        } catch (error) {
            const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
            return thunkApi.rejectWithValue(message)
        }
})
export const postRefundDetails = createAsyncThunk('order/post-refund', async (data, thunkApi) => {
    try {
        const token = thunkApi.getState().auth.userData.accessToken
        return await orderService.postRefundDetails(data, token)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const orderSlice = createSlice({
    name: 'order', 
    initialState, 
    reducers: {
        reset: (state) => {
            state.isLoading = false
            state.isSuccess = false
            state.isError = false
            state.message = ''
        }
    }, 
    extraReducers: (builder) => {
        builder
            .addCase(postOrder.pending, (state) => {
                state.isLoading = true
            })
            .addCase(postOrder.rejected, (state, action) => {
                state.isLoading = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(postOrder.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                state.orders.push(action.payload)
            })
            .addCase(getOrders.pending, (state) => {
                state.isLoading = true
            })
            .addCase(getOrders.rejected, (state, action) => {
                state.isLoading = false
                state.isSuccess = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(getOrders.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                state.orders = action.payload
            })
            .addCase(getSingleOrder.pending, (state) => {
                state.isLoading = true
            })
            .addCase(getSingleOrder.rejected, (state, action) => {
                state.isLoading = false
                state.isSuccess = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(getSingleOrder.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                state.message = action.payload
            })
            .addCase(updateStatus.pending, (state) => {
                state.isLoading = false
            })
            .addCase(updateStatus.rejected, (state, action) => {
                state.isLoading = false
                state.isSuccess = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(updateStatus.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                const updatedRecordIndex = state.orders.findIndex(
                    (order) => order._id === action.payload._id
                )
                // If the updated record is found in the orders array,  update it
                if (updatedRecordIndex !== -1) {
                    state.orders.splice(updatedRecordIndex, 1, action.payload)
                }
                
            })
            .addCase(getSales.pending, (state) => {
                state.isLoading = true
            })
            .addCase(getSales.rejected, (state, action) => {
                state.isLoading = false
                state.isSuccess = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(getSales.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                state.sales = action.payload
            })
            .addCase(postShippingDetails.pending, (state) => {
                state.isLoading = true
            })
            .addCase(postShippingDetails.rejected, (state, action) => {
                state.isLoading = false
                state.isSuccess = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(postShippingDetails.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                const updatedRecordIndex = state.orders.findIndex(
                    (order) => order._id === action.payload._id
                )
                // If the updated record is found in the orders array,  update it
                if (updatedRecordIndex !== -1) {
                    state.orders.splice(updatedRecordIndex, 1, action.payload)
                }
                
            })
            .addCase(postRefundDetails.pending, (state) => {
                state.isLoading = true
            })
            .addCase(postRefundDetails.rejected, (state, action) => {
                state.isLoading = false
                state.isSuccess = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(postRefundDetails.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                const updatedRecordIndex = state.orders.findIndex(
                    (order) => order._id === action.payload._id
                )
                // If the updated record is found in the orders array,  update it
                if (updatedRecordIndex !== -1) {
                    state.orders.splice(updatedRecordIndex, 1, action.payload)
                }
            })
            .addCase(getOrderChunk.fulfilled, (state,  action) => {
                state.data = action.payload.data === undefined ? [] : action.payload.data
                state.params = action.payload.params
                state.total = action.payload.totalPages
            })
    }
})

export const {reset} = orderSlice.actions
export default orderSlice.reducer

