import { createSlice, createAsyncThunk } from "@reduxjs/toolkit"
import { headerService } from "./headerService"
const initialState = {
  headers: [],  
  p_isLoading: false,  
  isSuccess: false,  
  isError: false,  
  message: "",
  data: [],
  total: 1,
  params: {}
}

export const addHeader = createAsyncThunk(
  "header/add",  
  async (productData, thunkApi) => {
  try {
  return await headerService.addHeader(productData)
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)

export const getHeaders = createAsyncThunk(
  "header/get",  
  async (_, thunkApi) => {
  try {
  return await headerService.getHeaders()
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)
export const getHeadersChunk = createAsyncThunk('header/get-chunk', async (params, thunkApi) => {
  try {
  const response = await headerService.getHeadersChunk(params)
  return {
    params,  
    data: response.data.users,  
    totalPages: response.data.total
    }
  } catch (error) {
  const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
  return thunkApi.rejectWithValue(message)
  }
})
//remove category api
export const removeHeader = createAsyncThunk(
  "header/remove-banner",  
  async (id, thunkApi) => {
  try {
  const token = thunkApi.getState().auth.userData.accessToken
  return await headerService.removeHeader(token, id)
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)

export const headerSlice = createSlice({
  name: "header",  
  initialState,  
  reducers: {
  reset: (state) => {
  state.p_isLoading = false
  state.isSuccess = false
  state.isError = false
  state.message = ""
  }
  },  
  extraReducers: (builder) => {
  builder
  .addCase(addHeader.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(addHeader.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(addHeader.fulfilled, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = true
    state.headers.push(action.payload)
  })
  .addCase(getHeaders.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(getHeaders.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(getHeaders.fulfilled, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = true
    console.log(action.payload)
    state.headers = action.payload
  })
  .addCase(removeHeader.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(removeHeader.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(removeHeader.fulfilled, (state, action) => {
    console.log(action)
    state.p_isLoading = false
    state.isSuccess = true
    state.message = action.payload
    //  // Find the index of the deleted record
    const deletedRecordIndex = state.headers.findIndex(
    (product) => product._id === action.payload._id
    )

    // // If the deleted record is found in the allUsers array, remove it
    if (deletedRecordIndex !== -1) {
    state.headers.splice(deletedRecordIndex, 1)
    }
  })
  .addCase(getHeadersChunk.fulfilled,  (state,  action) => {
    console.log(action.payload)
    state.data = action.payload.data === undefined ? [] : action.payload.data
    state.params = action.payload.params
    state.total = action.payload.totalPages
  })
  }
})
export const { reset } = headerSlice.actions

export default headerSlice.reducer
