// ** Reducers Imports
// import navbar from './navbar'
import layout from './layout'
import auth from './authentication'
import users from '@src/views/apps/user/store'
import authReducer from '../views/user/employee/store/authSlice'
import customerReducer from '../views/user/customer/store/customerSlice'
import productReducer from '../views/product/store/productSlice'
import categoryReducer from '../views/category/store/categorySlice'
import deliveryReducer from '../views/delivery/store/deliverySlice'
import orderReducer from '../views/order/store/orderSlice'
import ticketReducer from '../views/ticket/store/ticketSlice'
import promotionReducer from '../views/website/promotions/store/promotionSlice'
import bannerReducer from '../views/website/banners/store/bannerSlice'
import footerReducer from '../views/website/footers/store/footerSlice'
import headerReducer from '../views/website/headers/store/headerSlice'
import { combineReducers } from '@reduxjs/toolkit'
const rootReducer = combineReducers({
  auth,
  users,
  layout,
  employees:authReducer,
  customers:customerReducer,
  products:productReducer,
  category:categoryReducer,
  delivery:deliveryReducer,
  orders:orderReducer,
  tickets: ticketReducer,
  promotions: promotionReducer,
  banners: bannerReducer,
  footers:footerReducer,
  headers:headerReducer

})

export default rootReducer
