import axios from 'axios'
const URL = `${process.env.REACT_APP_BASE_URL}/api/codes`
// const URL = `http://localhost:3001/api/codes`
const addDeliveryCode = async (customer, token)  =>  {
    const config = {
        headers: {
            Authorization: `Bearer ${token}`
        }
    }
    const response = await axios.post(`${URL}/add-countrycodes`,  customer, config)
    return response.data
}
