const addDeliveryCodeThroughExcel = async (data, token) => {
    const config = {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${token}`
      },
      body: data
    }
  
    try {
      const response = await fetch(`${URL}/add-countrycodes-excel`, config)
      // Check if the response is unsuccessful
      if (!response.ok) {
        const data = await response.json()
        throw new Error(`${JSON.stringify(data)}`)
      }
      // Parse JSON response
      const data = await response.json() 
      return data
    } catch (error) {
      throw error
    }
  }
  
const getAllDeliveryCodes = async() => {
    const response = await axios.get(`${URL}/get-countrycodes`)
    return response.data
}

// fetch single customer for update
const getSingleDeliveryCode = async (id, token)  =>  {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = await axios.get(`${URL}/get-countrycodes/${id}`, config)
    return response.data
}

const updateDeliveryCode = async(userData, token)  =>  {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = await axios.put(`${URL}/update-countrycodes/${userData._id}`, userData, config)
    return response.data
}
const getDeliveryCodesChunk = async (params) => {
    const response = await axios.post(`${URL}/get-countrycodes-chunk`, params)
    return response
}
const removeDeliveryCode = (token, id)  =>  {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = axios.delete(`${URL}/remove-countrycodes/${id}`, config)
    return response.data
}


const deliveryService = {
    getAllDeliveryCodes, 
    addDeliveryCode, 
    removeDeliveryCode, 
    getSingleDeliveryCode, 
    updateDeliveryCode,
    getDeliveryCodesChunk,
    addDeliveryCodeThroughExcel
}


export default deliveryService