import { createSlice,  createAsyncThunk } from '@reduxjs/toolkit'
import deliveryService from './deliveryService'

// set the initialState
const initialState = {
    deliveryCodes: [], 
    isLoading: false, 
    isSuccess: false, 
    isError: false, 
    message: '', 
    response:'',
    data: [],
    total: 1,
    params: {}
}

export const addDeliveryCode = createAsyncThunk('deliveryCode/post',  async (data,  thunkApi) => {
    try {
        
    const token = thunkApi.getState().auth.userData.accessToken
    return deliveryService.addDeliveryCode(data, token)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const addDeliveryCodeThroughExcel = createAsyncThunk('deliveryCode/post-excel',  async (data,  thunkApi) => {
         
    try {     
    const token = thunkApi.getState().auth.userData.accessToken
    const response = await deliveryService.addDeliveryCodeThroughExcel(data, token)
    return response
    } catch (error) {
        const message = (error.Error && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const getAllDeliveryCodes = createAsyncThunk('deliveryCode/get',  async (customer,  thunkApi) => {
    try {
        
    return deliveryService.getAllDeliveryCodes()
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
export const getDeliveryCodesChunk = createAsyncThunk('auth/get-chunk-customer',  async (params,  thunkApi) => {
    try {
        const response = await deliveryService.getDeliveryCodesChunk(params)
        // return response
        return {
            params,
            data: response.data.users,
            totalPages: response.data.total
          }
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
//get single customer by id
export const getSingleDeliveryCode = createAsyncThunk('auth/get-single-customer',  async (id,  thunkApi) => {
    try {
        const token = thunkApi.getState().auth.userData.accessToken
        return deliveryService.getSingleDeliveryCode(id, token)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

// update user
export const updateDeliveryCode = createAsyncThunk('deliveryCode/update',  async (userData,  thunkApi) => {
    try {
        const token = thunkApi.getState().auth.userData.accessToken
        return deliveryService.updateDeliveryCode(userData,  token)
    } catch (error) {
        return thunkApi.rejectWithValue(error.error)
    }
})

//remove customer api
export const removeDeliveryCode = createAsyncThunk('deliveryCode/remove-single-customer',  async (id,  thunkApi) => {
    try {
        const token = thunkApi.getState().auth.userData.accessToken
        return deliveryService.removeDeliveryCode(token, id)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const deliverySlice = createSlice({
    name: 'delivery', 
    initialState, 
    reducers: {
        reset: (state) => {
            state.isLoading = false
            state.isSuccess = false
            state.isError = false
            state.message = ''
        }
    }, 
    extraReducers: (builder) => {
        builder 
            .addCase(addDeliveryCode.pending,  (state) => {
                state.isLoading = true
            })
            .addCase(addDeliveryCode.rejected,  (state,  action) => {
                state.isLoading = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(addDeliveryCode.fulfilled,  (state,  action) => {
                state.isLoading = false
                state.isSuccess = true
                state.isError = false
                state.deliveryCodes.push(action.payload.record)
            })
            .addCase(addDeliveryCodeThroughExcel.pending,  (state) => {
                state.isLoading = true
            })
            .addCase(addDeliveryCodeThroughExcel.rejected,  (state,  action) => {
                state.isLoading = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(addDeliveryCodeThroughExcel.fulfilled,  (state,  action) => {
                state.isLoading = false
                state.isSuccess = true
                state.isError = false
                state.deliveryCodes.push(...action.payload.response)
            })
            .addCase(getAllDeliveryCodes.pending,  (state) => {
                state.isLoading = true
            })
            .addCase(getAllDeliveryCodes.rejected,  (state,  action) => {
                
                state.isLoading = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(getAllDeliveryCodes.fulfilled,  (state,  action) => {
                state.deliveryCodes = [...action.payload.allCodes]
                state.isLoading = false
                state.isSuccess = true
                state.isError = false
            })
            .addCase(getSingleDeliveryCode.pending,  (state) => {
                state.isLoading = false
            })
            .addCase(getSingleDeliveryCode.rejected,  (state,  action) => {
                
                state.isLoading = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(getSingleDeliveryCode.fulfilled,  (state,  action) => {
                state.isLoading = false
                state.isSuccess = true
                state.isError = false
                state.message = action.payload
            })
            .addCase(updateDeliveryCode.pending,  (state) => {
                state.isLoading = false
            })
            .addCase(updateDeliveryCode.rejected,  (state,  action) => {
                
                state.isLoading = false
                state.isError = true
                state.isSuccess = false
                state.message = action.payload
            })
            .addCase(updateDeliveryCode.fulfilled,  (state,  action) => {
                state.isLoading = false
                state.isSuccess = true
                state.isError = false
                // Find the index of the updated record
                const updatedRecordIndex = state.deliveryCodes.findIndex(
                    (customer) => customer._id === action.payload._id
                )
                // If the updated record is found in the customers array,  update it
                if (updatedRecordIndex !== -1) {
                    state.deliveryCodes.splice(updatedRecordIndex, 1, action.payload)
                }
            })
            .addCase(removeDeliveryCode.pending,  (state) => {
                state.isLoading = true
            })
            .addCase(removeDeliveryCode.rejected,  (state,  action) => {
                state.isLoading = false
                state.isError = true
                state.message = action.payload.customer
            })
            .addCase(removeDeliveryCode.fulfilled,  (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                state.payload = action.payload

                //  // Find the index of the deconsted record
                const deletedRecordIndex = state.deliveryCodes.findIndex(
                    (customer) => customer._id === action.meta.arg
                )
  
                // // If the deconsted record is found in the allUsers array,  remove it
                if (deletedRecordIndex !== -1) {
                    state.deliveryCodes.splice(deletedRecordIndex,  1)
                }
            })
            .addCase(getDeliveryCodesChunk.fulfilled,  (state,  action) => {
                state.data = action.payload.data === undefined ? [] : action.payload.data
                state.params = action.payload.params
                state.total = action.payload.totalPages
            })
    }
})
export const { reset } = deliverySlice.actions
export default deliverySlice.reducer