import { createSlice, createAsyncThunk } from "@reduxjs/toolkit"
import { bannerService } from "./bannerService"
const initialState = {
  banners: [],  
  p_isLoading: false,  
  isSuccess: false,  
  isError: false,  
  message: "",
  data: [],
  total: 1,
  params: {}
}

export const addBanner = createAsyncThunk(
  "banner/add",  
  async (productData, thunkApi) => {
  try {
  return await bannerService.addBanner(productData)
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)

export const getBanners = createAsyncThunk(
  "banner/get",  
  async (_, thunkApi) => {
  try {
  return await bannerService.getBanners()
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)
export const getBannersChunk = createAsyncThunk('banner/get-chunk', async (params, thunkApi) => {
  try {
  const response = await bannerService.getBannersChunk(params)
  return {
    params,  
    data: response.data.users,  
    totalPages: response.data.total
    }
  } catch (error) {
  const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
  return thunkApi.rejectWithValue(message)
  }
})
//remove category api
export const removeBanner = createAsyncThunk(
  "banner/remove-banner",  
  async (id, thunkApi) => {
  try {
  const token = thunkApi.getState().auth.userData.accessToken
  return await bannerService.removeBanner(token, id)
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)

export const bannerSlice = createSlice({
  name: "banner",  
  initialState,  
  reducers: {
  reset: (state) => {
  state.p_isLoading = false
  state.isSuccess = false
  state.isError = false
  state.message = ""
  }
  },  
  extraReducers: (builder) => {
  builder
  .addCase(addBanner.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(addBanner.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(addBanner.fulfilled, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = true
    state.banners.push(action.payload)
  })
  .addCase(getBanners.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(getBanners.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(getBanners.fulfilled, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = true
    console.log(action.payload)
    state.banners = action.payload
  })
  .addCase(removeBanner.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(removeBanner.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(removeBanner.fulfilled, (state, action) => {
    console.log(action)
    state.p_isLoading = false
    state.isSuccess = true
    state.message = action.payload
    //  // Find the index of the deleted record
    const deletedRecordIndex = state.banners.findIndex(
    (product) => product._id === action.payload._id
    )

    // // If the deleted record is found in the allUsers array, remove it
    if (deletedRecordIndex !== -1) {
    state.banners.splice(deletedRecordIndex, 1)
    }
  })
  .addCase(getBannersChunk.fulfilled,  (state,  action) => {
    console.log(action.payload)
    state.data = action.payload.data === undefined ? [] : action.payload.data
    state.params = action.payload.params
    state.total = action.payload.totalPages
  })
  }
})
export const { reset } = bannerSlice.actions

export default bannerSlice.reducer
