import { createSlice, createAsyncThunk } from "@reduxjs/toolkit"
import { promotionService } from "./promotionService"
const initialState = {
  promotions: [],  
  p_isLoading: false,  
  isSuccess: false,  
  isError: false,  
  message: "",
  data: [],
  total: 1,
  params: {}
}

export const addPromotion = createAsyncThunk(
  "promotion/add",  
  async (productData, thunkApi) => {
  try {
  return await promotionService.addPromotion(productData)
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)

export const getPromotions = createAsyncThunk(
  "promotion/get",  
  async (_, thunkApi) => {
  try {
  return await promotionService.getPromotions()
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)
export const getPromotionsChunk = createAsyncThunk('promotion/get-chunk', async (params, thunkApi) => {
  try {
  const response = await promotionService.getPromotionsChunk(params)
  return {
    params,  
    data: response.data.users,  
    totalPages: response.data.total
    }
  } catch (error) {
  const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
  return thunkApi.rejectWithValue(message)
  }
})
//remove category api
export const removePromotion = createAsyncThunk(
  "promotion/remove-promotion",  
  async (id, thunkApi) => {
  try {
  const token = thunkApi.getState().auth.userData.accessToken
  return await promotionService.removePromotion(token, id)
  } catch (error) {
  const message =
    (error.response &&
    error.response.data &&
    error.response.data.message) ||
    error.message ||
    error.toString()
  return thunkApi.rejectWithValue(message)
  }
  }
)

export const promotionSlice = createSlice({
  name: "promotion",  
  initialState,  
  reducers: {
  reset: (state) => {
  state.p_isLoading = false
  state.isSuccess = false
  state.isError = false
  state.message = ""
  }
  },  
  extraReducers: (builder) => {
  builder
  .addCase(addPromotion.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(addPromotion.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(addPromotion.fulfilled, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = true
    state.promotions.push(action.payload)
  })
  .addCase(getPromotions.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(getPromotions.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(getPromotions.fulfilled, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = true
    console.log(action.payload)
    state.promotions = action.payload
  })
  .addCase(removePromotion.pending, (state) => {
    state.p_isLoading = true
  })
  .addCase(removePromotion.rejected, (state, action) => {
    state.p_isLoading = false
    state.isSuccess = false
    state.isError = true
    state.message = action.payload
  })
  .addCase(removePromotion.fulfilled, (state, action) => {
    console.log(action)
    state.p_isLoading = false
    state.isSuccess = true
    state.message = action.payload
    //  // Find the index of the deleted record
    const deletedRecordIndex = state.promotions.findIndex(
    (product) => product._id === action.payload._id
    )

    // // If the deleted record is found in the allUsers array, remove it
    if (deletedRecordIndex !== -1) {
    state.promotions.splice(deletedRecordIndex, 1)
    }
  })
  .addCase(getPromotionsChunk.fulfilled,  (state,  action) => {
    console.log(action.payload)
    state.data = action.payload.data === undefined ? [] : action.payload.data
    state.params = action.payload.params
    state.total = action.payload.totalPages
  })
  }
})
export const { reset } = promotionSlice.actions

export default promotionSlice.reducer
