import axios from 'axios'
const URL = `${process.env.REACT_APP_BASE_URL}/api/sitefeature`
// const URL = `http://localhost:3001/api/product`

const addFooter = async (data, token) => {
    const config = {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${token}`
      },
      body: data
    }
  
    try {
      const response = await fetch(`${URL}/add-footer`, config)
      // Check if the response is unsuccessful
      if (!response.ok) {
        const data = await response.json()
        throw new Error(`${JSON.stringify(data)}`)
      }
      // Parse JSON response
      const data = await response.json() 
      return data
    } catch (error) {
      throw error
    }
  }
  
const getFooters = async () => {
    const config = {
      method: 'GET'
    }
  
    try {
      const response = await fetch(`${URL}/get-footers`, config)
      // Check if the response is unsuccessful
      if (!response.ok) {
        const data = await response.json()
        throw new Error(`${JSON.stringify(data)}`)
      }
      // Parse JSON response
      const data = await response.json() 
      console.log(data[0].imageUrl)
      return data
    } catch (error) {
      throw error
    }
  }
  
const getFootersChunk = async (params) => {
    const response = await axios.post(`${URL}/get-footers-chunk`, params)
    return response
}

const removeFooter = async(token, id) => {
    const config = {
      method: 'DELETE'
    }
  
    try {
      const response = await fetch(`${URL}/remove-footer/${id}`, config)
      // Check if the response is unsuccessful
      if (!response.ok) {
        const data = await response.json()
        throw new Error(`${JSON.stringify(data)}`)
      }
      // Parse JSON response
      const data = await response.json() 
      console.log(data)
      return data
    } catch (error) {
      throw error
    }
    
}

export const footerService = {
    addFooter,  
    getFooters, 
    removeFooter,  
    getFootersChunk
}