import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import ticketService from './ticketService'

// set the initialState
const initialState = {
    tickets: [], 
    isLoading: false, 
    isSuccess: false, 
    isError: false, 
    message: '', 
    data: [],
    total: 1,
    params: {}
}

export const addTicket = createAsyncThunk('ticket/post', async (ticket, thunkApi) => {
    try {
        
    return await ticketService.addTicket(ticket)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const getTickets = createAsyncThunk('ticket/get', async (_, thunkApi) => {
    try {
        return await ticketService.getTickets()
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
export const getTicketsChunk = createAsyncThunk('ticket/get-chunk-ticket',  async (params,  thunkApi) => {
    try {
        const response = await ticketService.getTicketsChunk(params)
        // return response
        return {
            params,
            data: response.data.users,
            totalPages: response.data.total
          }
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const getSingleTicket = createAsyncThunk('ticket/get-single', async (ticket_id, thunkApi) => {
    try {
        return await ticketService.getSingleTicket(ticket_id)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
export const updateTicket = createAsyncThunk('ticket/update-ticket', async (data, thunkApi) => {
    try {
        return await ticketService.updateTicket(data)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const ticketSlice = createSlice({
    name: 'ticket', 
    initialState, 
    reducers: {
        reset: (state) => {
            state.isLoading = false
            state.isSuccess = false
            state.isError = false
            state.message = ''
        }
    }, 
    extraReducers: (builder) => {
        builder
            .addCase(addTicket.pending, (state) => {
                state.isLoading = true
            })
            .addCase(addTicket.rejected, (state, action) => {
                state.isLoading = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(addTicket.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                state.tickets.push(action.payload)
            })
            .addCase(updateTicket.pending, (state) => {
                state.isLoading = false
            })
            .addCase(updateTicket.rejected, (state, action) => {
                state.isLoading = false
                state.isSuccess = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(updateTicket.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                const updatedRecordIndex = state.tickets.findIndex(
                    (order) => order._id === action.payload._id
                )
                // If the updated record is found in the tickets array,  update it
                if (updatedRecordIndex !== -1) {
                    state.tickets.splice(updatedRecordIndex, 1, action.payload)
                }
                
            })
            .addCase(getTickets.pending, (state) => {
                state.isLoading = true
            })
            .addCase(getTickets.rejected, (state, action) => {
                state.isLoading = false
                state.isSuccess = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(getTickets.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                state.tickets = action.payload.tickets
            })
            .addCase(getSingleTicket.pending, (state) => {
                state.isLoading = true
            })
            .addCase(getSingleTicket.rejected, (state, action) => {
                state.isLoading = false
                state.isSuccess = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(getSingleTicket.fulfilled, (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                state.message = action.payload
            })
            .addCase(getTicketsChunk.fulfilled, (state,  action) => {
                state.data = action.payload.data === undefined ? [] : action.payload.data
                state.params = action.payload.params
                state.total = action.payload.totalPages
            })
            
    }
})

export const {reset} = ticketSlice.actions
export default ticketSlice.reducer

