import axios from 'axios'
const URL = `${process.env.REACT_APP_BASE_URL}/api/ticket`

// const URL = `http://localhost:3001/api/ticket`

const addTicket = async (orders) => {
    const response = await axios.post(`${URL}/add-ticket`, orders)
    return response.data
}

const getTickets = async () => {
    const response = await axios.get(`${URL}/get-tickets`)
    return response.data
}

const getTicketsChunk = async (params) => {
    const response = await axios.post(`${URL}/get-tickets-chunk`, params)
    return response
}

const getSingleTicket = async (order_id) => {
    const response = await axios.get(`${URL}/get-single-ticket/${order_id}`)
    return response.data
}

const updateTicket = async (data, token) => {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = await axios.put(`${URL}/update-ticket/${data.id}`, data, config)
    return response.data
}

const ticketService = {
    addTicket, 
    getTickets,
    getTicketsChunk,
    getSingleTicket, 
    updateTicket

}


export default ticketService