import { createSlice,  createAsyncThunk } from '@reduxjs/toolkit'
import categoryService from './categoryService'

// set the initialState
const initialState = {
    categories: [], 
    isLoading: false, 
    isSuccess: false, 
    isError: false, 
    message: '', 
    data: [],
    total: 1,
    params: {}
}

export const addCategory = createAsyncThunk('category/post',  async (category,  thunkApi) => {
    try {

            const token = thunkApi.getState().auth.userData.accessToken
            return await categoryService.postCategory(category,  token)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const getAllCategories = createAsyncThunk('category/get',  async (_, thunkApi) => {
    try {
    return await categoryService.getAllCategories()
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
export const getCategoriesChunk = createAsyncThunk('category/get-chunk-category',  async (params,  thunkApi) => {
    try {
        const response = await categoryService.getCategoriesChunk(params)
        // return response
        return {
            params,
            data: response.data.users,
            totalPages: response.data.total
          }
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})
//get single category by id
export const getSingleCategory = createAsyncThunk('auth/get-single-category',  async (id,  thunkApi) => {
    try {
        const token = thunkApi.getState().auth.userData.accessToken
        return await categoryService.getSingleCategory(id, token)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

// update user
export const updateCategory = createAsyncThunk('category/update',  async (userData,  thunkApi) => {
    try {

        const token = thunkApi.getState().auth.userData.accessToken
        return await categoryService.updateCategory(userData,  token)
    } catch (error) {
        return thunkApi.rejectWithValue(error.error)
    }
})

//remove category api
export const removeCategory = createAsyncThunk('category/remove-single-category',  async (id,  thunkApi) => {
    try {
        const token = thunkApi.getState().auth.userData.accessToken
        return await categoryService.removeCategory(token, id)
    } catch (error) {
        const message = (error.response && error.response.data && error.response.data.message) || error.message || error.toString()
        return thunkApi.rejectWithValue(message)
    }
})

export const categorySlice = createSlice({
    name: 'category', 
    initialState, 
    reducers: {
        reset: (state) => {
            state.isLoading = false
            state.isSuccess = false
            state.isError = false
            state.message = ''
        }
    }, 
    extraReducers: (builder) => {
        builder 
            .addCase(addCategory.pending,  (state) => {
                state.isLoading = true
            })
            .addCase(addCategory.rejected,  (state,  action) => {
                state.isLoading = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(addCategory.fulfilled,  (state,  action) => {
                state.isLoading = false
                state.isSuccess = true
                state.isError = false
                state.categories.push(action.payload.newCategory)
            })
            .addCase(getAllCategories.pending,  (state) => {
                state.isLoading = true
            })
            .addCase(getAllCategories.rejected,  (state,  action) => {
                
                state.isLoading = false
                state.isError = true
                state.message = action.payload
            })
            .addCase(getAllCategories.fulfilled,  (state,  action) => {
                state.categories = [...action.payload.categories]
                state.isLoading = false
                state.isSuccess = true
                state.isError = false
            })
            .addCase(getSingleCategory.pending,  (state) => {
                state.isLoading = true
            })
            .addCase(getSingleCategory.rejected,  (state,  action) => {
                
                state.isLoading = false
                state.isError = true
                state.categories = []
                state.message = action.payload
            })
            .addCase(getSingleCategory.fulfilled,  (state,  action) => {
                state.isLoading = false
                state.isSuccess = true
                state.isError = false
                state.message = action.payload
            })
            .addCase(updateCategory.pending,  (state) => {
                state.isLoading = true
            })
            .addCase(updateCategory.rejected,  (state,  action) => {
                
                state.isLoading = false
                state.isError = true
                state.isSuccess = false
                state.message = action.payload
            })
            .addCase(updateCategory.fulfilled,  (state,  action) => {
                state.isLoading = false
                state.isSuccess = true
                state.isError = false
                // Find the index of the updated record
                const updatedRecordIndex = state.categories.findIndex(
                    (category) => category._id === action.payload._id
                )
                // If the updated record is found in the categories array,  update it
                if (updatedRecordIndex !== -1) {
                    state.categories.splice(updatedRecordIndex, 1, action.payload)
                }
            })
            .addCase(removeCategory.pending,  (state) => {
                state.isLoading = true
            })
            .addCase(removeCategory.rejected,  (state,  action) => {
                state.isLoading = false
                state.isError = true
                state.message = action.payload.category
            })
            .addCase(removeCategory.fulfilled,  (state, action) => {
                state.isLoading = false
                state.isSuccess = true
                state.payload = action.payload
                //  // Find the index of the deleted record
                const deletedRecordIndex = state.categories.findIndex(
                    (category) => category._id === action.meta.arg
                )
  
                // // If the deleted record is found in the allUsers array,  remove it
                if (deletedRecordIndex !== -1) {
                    state.categories.splice(deletedRecordIndex,  1)
                }
            })
            .addCase(getCategoriesChunk.fulfilled,  (state,  action) => {
                state.data = action.payload.data === undefined ? [] : action.payload.data
                state.params = action.payload.params
                state.total = action.payload.totalPages
            })
    }
})
export const { reset } = categorySlice.actions
export default categorySlice.reducer