import axios from 'axios'
const URL = `${process.env.REACT_APP_BASE_URL}/api/users`
// const URL = `http://localhost:3001/api/users`

const registerUser = async (userData) => {
    const response = await axios.post(`${URL}/register`,  userData)
    if (response.data) {
        localStorage.setItem('user', JSON.stringify(response.data))
    }
    return response
}

const loginUser = async (userData) => { 
    const response = await axios.post(`${URL}/login`,  userData)
    if (response.data) {
        localStorage.setItem('user',  JSON.stringify(response.data))
    }
    return response
}

const sendResetMail = async (userData) => { 
    const response = await axios.post(`${URL}/reset-password`,  userData)
    return response
}


const addNewUser = async(userData, token) => {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = await axios.post(`${URL}/add-new-user`, userData, config)
    return response.data
}
const updateUser = async(userData, token) => {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = await axios.put(`${URL}/update-user/${userData._id}`, userData, config)
    return response.data
}

const getSingleUser = async (id, token) => {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = await axios.get(`${URL}/get-user/${id}`, config)
    return response.data
}

const getAllUsers = async (token) => {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = await axios.get(`${URL}/get-users`, config)
    return response.data
}
const getUserChunk = async (params) => {
    const response = await axios.post(`${URL}/get-users-chunk`, params)
    return response
}

const resetPassword = (token, id) => {
    const response = axios.post(`${URL}/reset-password/${token}`, id)
    return response.data
}
const removeUser = (token, id) => {
    const config = {
        headers: {
            Authorization:`Bearer ${token}`
        }
    }
    const response = axios.delete(`${URL}/remove-user/${id}`, config)
    return response.data
}

const logout = () => {
    localStorage.removeItem('userData')
}

const authService = {
    registerUser, 
    loginUser, 
    sendResetMail, 
    addNewUser, 
    updateUser, 
    getAllUsers, 
    resetPassword, 
    logout, 
    getSingleUser, 
    removeUser,
    getUserChunk
}

export default authService